.page-header {
  border-bottom: 1px solid #e6e6e6;
}

.page-header__container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.page-header__logo {
  max-width: 190px;
  width: 100%;
  height: 65px;
  transition: 0.3s;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-header__logo-image {
  margin-top: 10px;
  margin-bottom: 10px;
  padding-top: 2px;
}

@media (min-width: $tablet-width) {
  .page-header {
    border-bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .page-header__logo {
    height: 65px;
    width: 173px;
    padding-top: 6px;
  }
}

@media (min-width: $desktop-width) {
  .page-header__container {
    padding-top: 38px;
  }

  .page-header__logo {
    max-width: 210px;
    width: 202px;
    height: 77px;
    padding-top: 3px;
  }

  .page-header {
    &--with-background {
      &::after {
        content: "";
        z-index: -1;
        display: block;
        width: 50%;
        min-height: 696px;
        top: 0;
        right: 0;
        left: inherit;
        background-image: url("../img/index-background-desktop@1x.jpg");
        background-color: #63a73a;
        background-position: 0%;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
      }
    }
  }
}

@media (min-width: $desktop-width) and (min-resolution: 144dpi), (min-width: $desktop-width) and (min-resolution: 1.5dppx) {
  .page-header {
    &--with-background {
      &::after {
        background-image: url("../img/index-background-desktop@2x.jpg");
      }
    }
  }
}
